body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

@keyframes hands-radius {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.watch {
  background-color: beige;
  border: 5px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.watch__center {
  z-index: 1;
  background-color: #7fffd4;
  border-radius: 50%;
  width: 5.33333vmin;
  height: 5.33333vmin;
  position: absolute;
  top: 37.3333vmin;
  left: 37.3333vmin;
}

.watch__hand-minute {
  border: 2px solid inherit;
  transform-origin: bottom;
  background-color: #8a2be2;
  border-radius: 10px;
  width: 3.2vmin;
  height: 40vmin;
  animation: 5s linear infinite hands-radius;
  position: absolute;
  top: 0;
  left: 38.4vmin;
}

.watch__hand-second {
  border: 2px solid inherit;
  transform-origin: bottom;
  background-color: tomato;
  border-radius: 20px;
  width: 2.28571vmin;
  height: 20vmin;
  animation: 60s linear infinite hands-radius;
  position: absolute;
  top: 20vmin;
  left: 38.8571vmin;
}

